import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { DATES, InputTooltipProps, WindowComponent, WindowComponentProps, withTooltip } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, ContaFluxoCaixaRelRV, REPORT_EVENT, REPORT_TITLE } from './contaFluxoCaixaRelRV';

const DEFAULT_VALUES: ContaFluxoCaixaRelRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, DATES.value),
  dtFinalRequired: true,
  dtInicialRequired: true,
  exibirConfiguracaoDadosEmpresa: false,
};

const ContaFluxoCaixaReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<ContaFluxoCaixaRelRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });
  const [isEnabledIsMatriz, setIsEnabledIsMatriz] = useState(false);

  const carregarParametroEmpresa = () => {
    if (globalReducer.globalParameter.empresa.matriz === null) {
      setIsEnabledIsMatriz(true);
      formProps.getValues().matrizFilial = globalReducer.globalParameter.empresa.centralizar;
    }
  };

  const openExtraDados = () => {
    if (globalReducer.globalParameter.empresa) {
      return globalReducer.globalParameter.empresa.matriz !== null;
    }
  };

  const propToolTip = (messagem: string): InputTooltipProps => {
    return {
      message: messagem,
      placement: 'top-start',
    };
  };

  const acumularMatrizFilial = isEnabledIsMatriz ? (
    <Row className="mb-3">
      <Col>
        <CustomInput type="switch" label="Acumular Matriz/Filial" name="matrizFilial" id="idmatrizFilial" innerRef={formProps.register} />
        {withTooltip(propToolTip('Selecione caso queira gerar por matriz/filial'), 'matrizFilial')}
      </Col>
    </Row>
  ) : null;

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa) {
        carregarParametroEmpresa();
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent
      title="Classificação de Fluxo de Caixa - Direto"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter)]}
      wikiId={787}
    >
      {acumularMatrizFilial}
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        extraConfigOpened={openExtraDados()}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      />
    </WindowComponent>
  );
};

export default ContaFluxoCaixaReportView;
